
.Header{
    max-width: 90%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;

}

.navLinks {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.cleanList {
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around
  }

.cleanList li {
  cursor: pointer;
  margin-left: 20px;
  color: white;
}
.cleanList li a {
  text-decoration: none;
  color: black;
  font-size: 20px
}
.cleanList  li a:hover {
  opacity: 0.8;
}

.border{
  
  border-bottom: 3px solid orangered
}



